@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;500;700&display=swap'); */


body {
  margin: 0;
  font-family: 'Montserrat', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body.light-mode {
  background-color: #fff;
  color: #212529;
  transition: background-color 0.3s ease;
}
body.dark-mode {
  background-color: #232935; 
  color: #fff;
  transition: background-color 0.3s ease;
}

button {
  outline: none;
}

button:focus {
  outline: 0 !important;
}

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.font-weight-300 {
  font-weight: 300;
}

.font-weight-400 {
  font-weight: 400;
}

.font-weight-500 {
  font-weight: 500;
}

.font-weight-600 {
  font-weight: 600;
}

.font-weight-700 {
  font-weight: 700;
}

.nav-link {
  font-weight: 500;
}

.switch-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 2rem;
}

.btn-icon {
  background-color: transparent; 
  border: none; 
}
.btn-icon:hover {
  color: rgb(0, 174, 255) !important;
}

.home-section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-title {
  font-weight: 500;
  text-shadow:  0 1px #05377048
}

.home-buttons {
  display: flex;
  justify-content: center;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.section-title {
  font-weight: 600;
  border-bottom: 4px solid rgb(0, 174, 255);
  /* text-shadow:  0 1px #05377048 */
}

.image {
  padding: 2rem;
  max-width: 25rem;
}

.about-card {
  padding: 1rem;
  max-width: 50rem;
}

.about-skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.about-skills-title {
  padding-top: 2rem;
  font-weight: 500;
  font-style: italic;
}

.about-description {
  font-weight: 400;
  font-size: 1.2rem;
}

.card {
  display: flex;
  max-width: 100vh;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,.06),0 2px 5px 0 rgba(0,0,0,.2);
}

.icon {
  margin: 1rem
}

.project-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.project-card-deck {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: left;
  max-width: 70rem;
}

.project-card-light {
  margin: 1rem;
  max-width: 30rem;
  color: #212529;
}

.project-card-dark {
  margin: 1rem;
  max-width: 30rem;
  background-color: #2d3542 !important;
  color: white;
}

.project-card-img {
  object-fit: cover;
  max-height: 15rem;
}

.project-card-title {
  font-weight: 600 !important;
  font-style: italic;
}

.project-card-text {
  font-weight: 400;
  font-size: 1.2rem;
}

.project-button {
  float: right;
  padding: 0 !important;
}

.footer {
  background-color: rgba(0,0,0,.03);
  margin-top: 2rem;
  padding-top: 2rem;
  padding-bottom: 1rem;
}

.blue {
  color: rgb(0, 174, 255);
}

.blue-href {
  color: rgb(0, 174, 255);
  text-decoration: none;
}

.blue-href:hover {
  color: rgb(0, 174, 255);  
  text-decoration: underline;
}
